@import '~common/styles/colors';

.profilePhoto {
  padding: 0 12px;
}

.profilePhotoImage {
  width: 300px;
  height: 300px;
  border-top-left-radius: 5%;
  border-top-right-radius: 5%;
  border-bottom-right-radius: 5%;
  border-bottom-left-radius: 10%;
  object-fit: cover;
  object-position: top;
}

.skillsContainer {
  margin-top: 36px;
}

.personalBlock {
  padding: 0 12px;
  h2 {
    margin: 18px 0 4px 0;
  }
}
.personalMainPoints {
  p {
    display: inline-block;
    padding: 0 16px 0 8px;
    position: relative;
    margin-bottom: 0;
    &::after {
      content: "-";
      position: absolute;
      display: inline-block;
      right: 0;
      top: 0;
    }
    &:first-child {
      padding-left: 0;
      color: map-get($color-primary, darkest);
    }
    &:last-child {
      padding-right: 0;
      &::after {
        display: none;
      }
    }
  }
}