@import '~common/styles/colors';
@import '~common/styles/mixins';

.container {
  padding: 24px 12px;
}

.portfolioItem {
  padding: 0 12px;
  box-sizing: border-box;
  margin-bottom: 18px;
}
.portfolioThumbnail {
  height: 300px;
  background-size: cover;
  margin-bottom: 12px;
  box-sizing: border-box;
  cursor: pointer;
  width: 100%;
  background-color: $bg-colors-light;
  position: relative;
  .portfolioThumbnailOverlay {
    display: flex;
    align-items: flex-end;
    top: 0;
    right: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    transition: opacity 0.15s ease-in;
    opacity: 0;
    padding: 6px 12px;
    box-sizing: border-box;
    background-color: unquote(map-get($color-primary, darkest) + 'AA');
  }
  &:hover {
    .portfolioThumbnailOverlay {
      opacity: 1;
    }
  }
  p {
    color: white;
    cursor: default;
  }
}

@include breakpoint-md-down() {
  .portfolioItem {
    padding: 0;
  }
}