@import '~common/styles/colors';
@import '~common/styles/mixins';

.container {
  @include panel-shadow();
  padding: $content-spacing-inner;
  max-width: map-get($content-max-width, 'md');
  margin: 0 auto;
  display: flex;
}

.buttonSlot {
  display: block;
  width: 180px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
}

.appBarItem {
  display: none;
}

@include breakpoint-sm-up() {
  .container {
    padding: $content-spacing-inner ($content-spacing-inner + $content-spacing-outer) * 2;
  }
  .buttonSlot {
    margin: -5px 0;
    display: block;
    width: 180px;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .appBarItem {
    display: block;
  }
  .navItems {
    flex-grow: 1;
    flex-basis: auto;
    flex-shrink: 0;
  }
  .appBarItem {
    a {
      position: relative;
      padding: 25px 12px;
      text-decoration: none;
      cursor: pointer;
      font-size: font-size(14);
      color: $typography-colors-main;
      font-weight: 300;
      &::after {
        content: " ";
        background-color: transparent;
        transition: background-color .1s ease-in;
        height: 0;
      }
      &:hover {
        color: map-get($color-primary, dark);
        &:after {
          content: " ";
          position: absolute;
          bottom: 1px;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: map-get($color-primary, dark);
        }
      }
    }
  }
}
