@import '~common/styles/mixins';

.container {
  display: flex;
  flex-wrap: wrap;
}

.item {
  display: block;
}

.justify-center {
  justify-content: center;
}

.justify-end {
  justify-content: flex-end;
}

.justify-start {
  justify-content: flex-start;
}

.no-wrap {
  flex-wrap: nowrap;
}

.align-items-center {
  align-items: center;
}

.align-items-end {
  align-items: flex-end;
}

.align-items-start {
  align-items: flex-start;
}

.column {
  flex-direction: column;
}

.column-reverse {
  flex-direction: column-reverse;
}

.row {
  flex-direction: row;
}

.row-reverse {
  flex-direction: row-reverse;
}

$columns: 12;
$size: 100 / 12;

@function portion($colCount) {
  @return unquote(($size * $colCount) + '%');
}

@include breakpoint-xs-up() {
  @for $i from 1 through $columns {
      .xs-#{$i} {
        flex-basis: portion($i);
        width: portion($i);
        box-sizing: border-box;
      }
  }
  .xs-auto {
    flex-basis: auto;
    width: auto;
    box-sizing: border-box;
    flex-grow: 1;
    flex-shrink: 1;
  }
}

