$breakpoints: (
  'xs': 0,
  'sm':  600px,
  'md': 960px,
  'lg':  1280px,
  'xl':  1920px,
);

$content-max-width: (
  md: 1228px,
);

$content-spacing: (
  'outer': 24px,
  'inner': 24px,
);

$content-spacing-outer: map-get($content-spacing, 'outer');
$content-spacing-inner: map-get($content-spacing, 'inner');

$font-sizes: (
  12: 0.75rem,
  14: 0.875rem,
  16: 1rem,
  18: 1.125rem,
  20: 1.25rem,
  24: 1.55rem,
  28: 1.75rem,
  32: 2rem,
);

@function font-size($size) {
  @return map-get($font-sizes, $size);
}