@import './variables';

@mixin panel-shadow() {
  box-shadow: 0 3px 7px 0 rgba(0,0,0,.05);
}

/**
  * BREAKPOINTS
  */

@mixin breakpoint-xs-up() {
  @media (min-width: map-get($breakpoints, 'xs')) {
    @content;
  }
}

@mixin breakpoint-sm-up() {
  @media (min-width: map-get($breakpoints, 'sm')) {
    @content;
  }
}

@mixin breakpoint-sm-down() {
  @media (max-width: map-get($breakpoints, 'sm') - 1) {
    @content;
  }
}

@mixin breakpoint-md-up() {
  @media (min-width: map-get($breakpoints, 'md')) {
    @content;
  }
}

@mixin breakpoint-md-down() {
  @media (max-width: map-get($breakpoints, 'md') - 1) {
    @content;
  }
}

@mixin breakpoint-lg-up() {
  @media (min-width: map-get($breakpoints, 'lg')) {
    @content;
  }
}

@mixin breakpoint-lg-down() {
  @media (max-width: map-get($breakpoints, 'lg') - 1) {
    @content;
  }
}

@mixin breakpoint-xl-up() {
  @media (min-width: map-get($breakpoints, 'xl')) {
    @content;
  }
}

@mixin breakpoint-xl-down() {
  @media (max-width: map-get($breakpoints, 'xl') - 1) {
    @content;
  }
}