@import '~common/styles/colors';
@import '~common/styles/variables';
@import '~common/styles/mixins';

.container {
  padding: 24px 12px;
}

.expertiseRow {
  margin-bottom: 20px;
  word-break: break-all;
  div {
    &:first-child {
      margin-right: 14px;
      img {
        width: 52px;
      }
    }
    &:last-child {
      flex-grow: 1;
      flex-basis: auto;
      flex-shrink: 1;
      h3, p {
        margin-bottom: 0;
      }
      h3 {
        margin-right: 14px;
      }
      p:last-child {
        font-size: font-size(14);
        color: $typography-colors-lightest;
      }
    }
  }
}

.availabilityBlock {
  h2 {
    margin-bottom: 14px;
  }
  h3.languagesTitle {
    margin: 30px 0 14px 0;
  }
  p.timezone {
    margin: 0;
    font-size: font-size(14);
    color: $typography-colors-lightest;
  }
  .languagesBlock {
    margin: 0 0 14px 0;
  }
  iframe {
    width: 100%;
    border: 0;
    background-color: $bg-colors-main;
    min-height: 90vw;
  }
}

@include breakpoint-md-up() {
  .availabilityBlock {
    iframe {
      width: 100%;
      min-height: 33vw;
    }
  }
}