@import '~common/styles/colors';
@import '~common/styles/variables';
@import '~common/styles/mixins';

@keyframes fadeIn {
	0% {
			opacity: 0;
	}
	100% {
			opacity: 1;
	}
}

.modal {
	height: 100%;
	flex-wrap: nowrap;
}

.modalBar {
	background-color: white;
	padding: 8px;
}

.modalBarProfileInfo {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 0 1rem;
	p {
		margin: 0;
		&:first-child {
			font-size: font-size(16);
			margin-bottom: .75rem;
		}
		&:last-child {
			font-size: font-size(14);
			color: map-get($typography-colors, light);
			margin-bottom: 0;
		}
	}
}

.modalButtonClose {
	background: none;
	border: 0;
	cursor: pointer;
}

.profileLogo {
	height: 60px;
	width: 60px;
	object-fit: cover;
}

.galleryButton {
	background-size: cover;
	background-image: none;
	background-color: transparent;
	border-left: 1px solid white;
	border-right: 1px solid white;
	border-bottom: 1px solid white;
	padding: 1rem;
	opacity: .5;
	transition: opacity .25s ease-in;
	&:first-child {
		border-top: 1px solid white;
	}
	&:hover {
		opacity: 1;
	}
	img {
		height: 80px;
		width: 80px;
		object-fit: cover;
	}
}

.galleryButtonActive {
	opacity: 1;
}

.gallerySelected {
	display: flex;
	max-height: 80%;
	max-width: 80%;
	margin: 0 auto;
	object-fit: contain;
}

.gallerySelectedContainer {
	padding: 2rem;
	width: 100%;
	min-height: 400px;
	display: flex;
	align-items: center;
	justify-content: center;
}

/**
 * Modal content
 */

@include breakpoint-md-down() {
	.modalContent {
		overflow-y: auto;
	}
	.modalContentDescription {
		overflow-y: hidden;
	}
	.modalContentScreenshots {
		flex-direction: column;
	}
	.modalContentScreenshotsNavigation {
		flex-direction: row;
	}
	.gallerySelected {
		width: 100%;
	}
}
@include breakpoint-md-up() {
	.modalContent {
		/** */
		overflow-y: hidden;
	}
	.modalContentDescription {
		overflow-y: auto;
	}
	.modalContentScreenshots {
		flex-direction: row;
	}
	.modalContentScreenshotsNavigation {
		flex-direction: column;
	}
}

.modalContentScreenshots {
	background-color: map-get($bg-colors, light);
	animation-name: fadeIn;
	animation-iteration-count: 1;
	animation-timing-function: ease-in-out;
	animation-duration: .25s;
	animation-fill-mode: forwards;
	padding-bottom: 2rem;
}

.modalContentDescription {
	background-color: white;
	animation-name: fadeIn;
	animation-iteration-count: 1;
	animation-timing-function: ease-in-out;
	animation-duration: .25s;
	animation-fill-mode:forwards;
	padding: .5rem 2rem;
	padding-bottom: 2rem;
}