@import '~common/styles/colors';
@import '~common/styles/mixins';


@keyframes gradient {
	0% {
			background-position: 0% 50%;
	}
	50% {
			background-position: 100% 50%;
	}
	100% {
			background-position: 0% 50%;
	}
}

.loadingIndicator {
	animation: gradient 1.5s ease infinite;
	background: linear-gradient(-45deg, map-get($color-primary, main), map-get($color-primary, light), map-get($color-primary, main), map-get($color-primary, darkest));
	background-size: 400% 400%;
	color: white;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 80px;
	width: 80px;
	p {
		margin: 0;
		color: white;
		font-size: font-size(14);
	}
}