@import '~common/styles/colors';
@import '~common/styles/mixins';

.contentOuter {
  position: relative;
  padding: 72px $content-spacing-outer 72px $content-spacing-outer;
  &::before {
    content: " ";
    display: block;
    position: absolute;
    height: 300px;
    width: 100%;
    background: linear-gradient(168deg, map-get($color-primary, darkest) 0%, map-get($color-primary, dark) 35%, map-get($color-primary, light) 100%);
    top: 0;
    left: 0;
    z-index: -2;
  }
  &::after {
    content: " ";
    z-index: -1;
    display: block;
    position: absolute;
    height: 300px;
    width: 100%;
    background-image: url('../../../assets//icons/floating-cogs.svg');
    top: 0;
    left: 0;
  }
}

.contentInner {
  @include panel-shadow();
  max-width: map-get($content-max-width, md);
  background-color: white;
  min-height: 400px;
  display: block;
  margin: 0 auto;
  position: relative;
}