body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes skeletonAnimation{
	0%{
			transform:translate3d(-30%,0,0)
	}
	to{
			transform:translate3d(30%,0,0)
	}
}
@keyframes skeletonAnimation{
	0%{
			transform:translate3d(-30%,0,0)
	}
	to{
			transform:translate3d(30%,0,0)
	}
}

.skeleton-item{
	direction:ltr;
	position:relative;
	display:flex;
	flex-wrap:wrap;
	padding:30px 15px 15px;
	overflow:hidden;
	margin-bottom:30px;
	background-color:#fff;
	border:1px solid #e6e6e6;
	border-radius:2px
}

.skeleton-item.skeleton-borderless {
  border: 0;
}

.skeleton-item,.skeleton-item *,.skeleton-item :after,.skeleton-item :before{
	box-sizing:border-box
}

.skeleton-item:before{
	content:" ";
	position:absolute;
	top:0;
	right:0;
	bottom:0;
	left:50%;
	z-index:1;
	width:500%;
	margin-left:-250%;
	animation: skeletonAnimation .8s linear infinite;
	background: linear-gradient(90deg,hsla(0,0%,100%,0) 46%,hsla(0,0%,100%,.35) 50%,hsla(0,0%,100%,0) 54%) 50% 50%
}
.skeleton-item>*{
	flex:1 1 auto;
	display:flex;
	flex-flow:column;
	padding-right:15px;
	padding-left:15px
}
.skeleton-row{
	display:flex;
	flex-wrap:wrap;
	margin-bottom:7.5px
}
.skeleton-row div{
	height:10px;
	margin-bottom:7.5px;
	background-color:#ced4da
}
.skeleton-row .skeleton-big, .skeleton-row.big div{
	height:20px;
	margin-bottom:15px
}
.skeleton-row .skeleton-empty{
	background-color:hsla(0,0%,100%,0)
}
.skeleton-col-2{
	flex:0 0 16.66667%
}
.skeleton-col-4{
	flex:0 0 33.33333%
}
.skeleton-col-6{
	flex:0 0 50%
}
.skeleton-col-8{
	flex:0 0 66.66667%
}
.skeleton-col-10{
	flex:0 0 83.33333%
}
.skeleton-col-12{
	flex:0 0 100%
}
.skeleton-avatar{
	position:relative;
	width:100%;
	min-width:60px;
	background-color:#ced4da;
	margin-bottom:15px;
	border-radius:50%;
	overflow:hidden
}
.skeleton-avatar:before{
	content:" ";
	display:block;
	padding-top:100%
}
.skeleton-picture{
	width:100%;
	height:120px;
	background-color:#ced4da;
	margin-bottom:15px
}