$primary: '';
$secondary: '';

$blue: (
  50: #e3f2fd,
  100: #bbdefb,
  200: #90caf9,
  300: #64b5f6,
  400: #42a5f5,
  500: #2196f3,
  600: #1e88e5,
  700: #1976d2,
  800: #1565c0,
  900: #0d47a1,
);

$amber: (
  50: #fff8e1,
  100: #ffecb3,
  200: #ffe082,
  300: #ffd54f,
  400: #ffca28,
  500: #ffc107,
  600: #ffb300,
  700: #ffa000,
  800: #ff8f00,
  900: #ff6f00,
);

$grey: (
  50: #fafafa,
  100: #f5f5f5,
  200:#eeeeee,
  300:#e0e0e0,
  400:#bdbdbd,
  500:#9e9e9e,
  600:#757575,
  700:#616161,
  800:#424242,
  900:#212121,
  1000: #111111,
  1100: #000000,
);

$bg-colors: (
  main: map-get($grey, 300),
  light: map-get($grey, 200),
  lightest: map-get($grey, 50),
  dark: map-get($grey, 500),
  darkest: map-get($grey, 600),
);

$bg-colors-dark: map-get($bg-colors, dark);
$bg-colors-darkest: map-get($bg-colors, darkest);
$bg-colors-main: map-get($bg-colors, main);
$bg-colors-light: map-get($bg-colors, light);
$bg-colors-lightest: map-get($bg-colors, lightest);

$typography-colors: (
  main: map-get($grey, 900),
  light: map-get($grey, 600),
  lightest: map-get($grey, 500),
  dark: map-get($grey, 1000),
  darkest: map-get($grey, 1100),
);

$typography-colors-dark: map-get($typography-colors, dark);
$typography-colors-darkest: map-get($typography-colors, darkest);
$typography-colors-main: map-get($typography-colors, main);
$typography-colors-light: map-get($typography-colors, light);
$typography-colors-lightest: map-get($typography-colors, lightest);

$color-primary: (
  lightest: map-get($blue, 100),
  light: map-get($blue, 300),
  main: map-get($blue, 500),
  dark: map-get($blue, 700),
  darkest: map-get($blue, 900),
);

$color-secondary: (
  lightest: map-get($amber, 100),
  light: map-get($amber, 300),
  main: map-get($amber, 500),
  dark: map-get($amber, 700),
  darkest: map-get($amber, 900),
);