@import '~common/styles/colors';
@import '~common/styles/variables';

.container {
  padding: 24px 12px;
}

.educationHistoryPrimary {
  flex-grow: 1;
  flex-basis: auto;
  flex-shrink: 0;
  p {
    &:first-child {
      margin-bottom: .1rem;
    }
    &:nth-child(2) {
      color: $typography-colors-lightest;
      margin-top: .25rem;
      font-size: font-size(12);
      margin-bottom: 1rem;
    }
  }
}

.educationHistorySecondary {
  p {
    color: $typography-colors-lightest;
    font-size: font-size(12);
  }
  margin-top: 0;
  margin-bottom: 1rem;
}