@import '~common/styles/colors';

.tag {
  color: map-get($typography-colors, light);
  background: transparent;
  display: inline-block;
  font-size: 0.775rem;
  line-height: 0.975rem;
  font-weight: 500;
  border: 1px solid map-get($bg-colors, main);
  border-radius: 12px;
  padding: 2px 16px;
  margin-right: 8px;
  margin-bottom: 8px;
  cursor: default;
}
