@import '~common/styles/colors';
@import '~common/styles/variables';

.container {
  padding: 24px 12px;
}

.companyLogo {
  width: 64px;
  min-height: 32px;
  max-height: 64px;
  object-fit: contain;
  margin-right: 12px;
}

.employmentDetails {
  position: relative;
  padding-left: 32px;
  padding-bottom: 20px;
  &::before {
    position: absolute;
    left: 0;
    top: 12px;
    content: "\B7";
    color: $bg-colors-dark;
    font-size: font-size(14);
    transform: scale(6);
  }
  &::after {
    content: "";
    position: absolute;
    left: .1rem;
    top: 55px;
    bottom: 0;
    border-left: 1px dashed $bg-colors-dark;
  }
  h3 {
    margin-bottom: 4px;
  }
  .technologyDetails {
    margin-top: 36px;
  }
  .technologyDetails,
  .subDetails {
    p {
      display: inline-block;
      margin: 0;
      margin-bottom: 16px;
      color: $typography-colors-lightest;
      font-size: font-size(12);
      &:first-child {
        &::after {
          content: "\B7";
          margin-left: 8px;
          margin-right: 8px;
        }
      }
      &:last-child {
        text-transform: uppercase;
      }
    }
  }
  ul {
    list-style-type: none;
    padding-left: 3rem;
    margin: 0;
    li {
      position: relative;
      margin-bottom: 1rem;
      font-size: font-size(14);
      color: $typography-colors-main;
      &::before {
        position: absolute;
        left: -3rem;
        top: -.85rem;
        content: "\B7";
        font-size: font-size(32);
      }
    }
  }
}